.x-overlay{
    position: absolute;
    width: 35px !important;
    height: 35px !important;
    opacity: 0.4;
}
.u-tooltip {
    width: 500px !important;
    position: static;
    
}

.u-tooltip .rc-tooltip-inner {
    background-color: rgb(255, 248, 210);
    color: black;
    border-radius: 0;  
    box-shadow: 
    0 0 1px 2px #5f5c42,
    0 0px 10px 1px rgb(8, 8, 8);
    
    
}

.headingtest {
    color: black;
    background-color: white;
    width: 100%;
    height: 90px;
    
}
.panel-btn {
    border-color: black !important;
    background-color: rgb(49, 49, 49) !important;
    color: gold !important;
    border-radius: 0px !important;
    width: 330px;
    text-align: left !important;
}
.panel {
    color: gold !important;
    margin: 10px;
    padding: 0px;
    float: left;
    width: 330px;
    box-shadow: 
        0 0 1px 2px #5f5c42,
        0 0px 10px 2px rgb(8, 8, 8);
}

.panel:hover {
    
    background-color: black !important;
    box-shadow: 
        0 0 1px 2px #5f5c42,
        0 0px 10px 2px rgb(8, 8, 8);
}

.whitespace {
    opacity: 0.0;
}
.tree img {
    width: 40px;
    height: 40px;
}

.unit {
    /* border-top: 1px solid white; */
    width: 35px !important;
    height: 35px !important;
    margin-bottom: 5px;
    margin-right: 5px;
    box-shadow: 
            0 0 1px 2px #5f5c42;
            
    /* box-shadow: 
            0 0 1px 2px #5f5c42;  
            0 0px 15px 3px rgb(255, 247, 178); */  
}

.unit:hover {

    outline: 2px solid rgb(66, 126, 255);
    box-shadow: 
            0 0 5px 5px rgb(121, 119, 110);
    
}


.unit-fu {
    outline: 2px dashed rgb(255, 216, 45);

    /* box-shadow: 
            0 0 1px 1px rgb(179, 174, 150),
            0 0 6px 3px rgb(255, 189, 65); */
}

.unit-na {
    opacity: 0.5;
    box-shadow: none; 
}

.unit-na:hover {
    box-shadow: none; 
            
}
.age {
    border-radius: 25px !important;
    width: 30px !important;
    height: 30px !important;
    margin-right: 10px;
    opacity: 0.7;
    border-radius: 0px;
    box-shadow: 
            0 0 1px 2px #5f5c42  ,
            0 0px 5px 1px rgb(255, 217, 113);
}

.age-empty {
    opacity: 0.0 !important;
}

.panel:focus {
    width: 800px;
}



.panel-space {
    font-size: 12px;
    color: white;
    padding: 5px;
    background-color: rgb(49, 49, 49) !important;
}

.panel-space:hover {
    background-color: rgb(24, 24, 24) !important;
}

.img {
    
    outline: 5px solid grey;
    outline-style: ridge;
    /* box-shadow: 
            0 0 5px 1px #ffe600  ,
            0 0 5px 1px rgb(255, 255, 255); */
}